import playerAPI from '../../../core/player-api-decorator';

import { IRootContainer } from '../../root-container/types';
import { IPlaybackEngine } from '../../playback-engine/types';


export default class Thumbnails {
  static moduleName = 'thumbnails';
  static dependencies = ['rootContainer', 'engine'];

  private _engine: IPlaybackEngine;

  private _config: any;
  private _node: HTMLElement;
  private _lowLoader: HTMLImageElement;
  private _highLoader: HTMLImageElement;
  private _textBlock: HTMLDivElement;
  private _node_low: any;
  private _node_high: any;

  constructor({
    rootContainer,
    engine,
  }: {
    rootContainer: IRootContainer;
    engine: IPlaybackEngine;
  }) {
    this._engine = engine;

    this._initUI();
    rootContainer.appendComponentNode(this.node);
  }

  private _initUI() {
    this._lowLoader = document.createElement('img');
    this._lowLoader.width = 1;
    this._lowLoader.height = 1;
    this._lowLoader.style.opacity = '0.1';

    this._highLoader = document.createElement('img');
    this._highLoader.width = 1;
    this._highLoader.height = 1;
    this._highLoader.style.opacity = '0.1';

    this._textBlock = document.createElement('div');

    this._node = document.createElement('div');

    this._node.style.display = "flex";
    this._node.style.alignItems = "center";
    this._node.style.flexDirection = "column-reverse";
    this._node.style.width = "180px";
    this._node.style.height = "90px";

    this._node_high = document.createElement('div');
    this._node_high.style.position = 'absolute';
    this._node_high.style.zIndex = '2';
    this._node_high.style.top = '0';
    this._node_high.style.bottom = '0';
    this._node_high.style.left = '0';
    this._node_high.style.right = '0';

    this._node_low = document.createElement('div');
    this._node_low.style.position = 'absolute';
    this._node_low.style.zIndex = '1';
    this._node_low.style.top = '0';
    this._node_low.style.bottom = '0';
    this._node_low.style.left = '0';
    this._node_low.style.right = '0';

    this._textBlock.style.backgroundColor = "rgba(0,0,0,0.8)";
    this._textBlock.style.padding = "2px 5px";
    this._textBlock.style.position = 'relative';
    this._textBlock.style.zIndex = '3';
    this._textBlock.style.bottom = '-5px';

    this._node.appendChild(this._textBlock);
    this._node.appendChild(this._lowLoader);
    this._node.appendChild(this._highLoader);
    this._node.appendChild(this._node_low);
    this._node.appendChild(this._node_high);
  }

  get node(): HTMLElement {
    return this._node;
  }

  @playerAPI()
  setConfig(config: any) {
    this._config = config;
  }

  @playerAPI()
  getAt(second: number) {
    const duration = this._engine.getDurationTime();
    if (!duration) {
      return;
    }
    const playedPercent = second / duration;
    const { framesCount } = this._config;
    const neededFrame = Math.round(framesCount * playedPercent);

    return this._config.qualities.map((quality: any) => {
      const framesInSprite = quality.framesInSprite.vert * quality.framesInSprite.horz;
      const frameNumberInSprite = neededFrame % framesInSprite;
      const spriteNumber = Math.floor(neededFrame / framesInSprite);
      const horzPositionInSprite = frameNumberInSprite % quality.framesInSprite.horz;
      const vertPositionInSprite = Math.floor(frameNumberInSprite / quality.framesInSprite.vert);
      const url = `https://storage.googleapis.com/video-player-media-server-static/thumbnails/${quality.spriteNameMask.replace('%d', spriteNumber)}`;

      return {
        frameSize: quality.frameSize,
        framesInSprite: ((spriteNumber + 1) * framesInSprite <= framesCount) ? quality.framesInSprite: {
          horz: Math.min((framesCount % framesInSprite), quality.framesInSprite.horz),
          vert: Math.ceil((framesCount % framesInSprite) / quality.framesInSprite.vert),
        },
        framePositionInSprite: {
          vert: vertPositionInSprite,
          horz: horzPositionInSprite,
        },
        spriteUrl: url,
      }
    });
  }

  @playerAPI()
  showAt(second: number) {
    const config = this.getAt(second);
    if (!config) {
      return;
    }
    const lowQuality = config[0];
    const highQuality = config[1];

    if (this._highLoader.src === highQuality.spriteUrl) {
      this._applyHighQuality(highQuality);
    } else {
      this._node_high.style.background = '';
      this._highLoader.onload = () => {
        this._applyHighQuality(highQuality);
      }
      this._highLoader.src = highQuality.spriteUrl;
    }

    if (this._lowLoader.src === lowQuality.spriteUrl) {
      this._applyLowQuality(lowQuality);
    } else {
      this._node_low.style.background = '';
      this._lowLoader.onload = () => {
        this._applyLowQuality(lowQuality);
      }
      this._lowLoader.src = lowQuality.spriteUrl;
    }
  }

  private _applyLowQuality(quality: any) {
    const viewWidth = this._node_low.offsetWidth;
    const viewHeight = this._node_low.offsetHeight;

    const backgroudWidth = viewWidth * quality.framesInSprite.horz;
    const backgroundHeight = viewHeight * quality.framesInSprite.vert;

    this._node_low.style.background = `url('${quality.spriteUrl}') -${viewWidth * quality.framePositionInSprite.horz}px -${viewHeight * quality.framePositionInSprite.vert}px / ${backgroudWidth}px ${backgroundHeight}px`
  }

  private _applyHighQuality(quality: any) {
    const viewWidth = this._node_high.offsetWidth;
    const viewHeight = this._node_high.offsetHeight;

    const backgroudWidth = viewWidth * quality.framesInSprite.horz;
    const backgroundHeight = viewHeight * quality.framesInSprite.vert;

    this._node_high.style.background = `url('${quality.spriteUrl}') -${viewWidth * quality.framePositionInSprite.horz}px -${viewHeight * quality.framePositionInSprite.vert}px / ${backgroudWidth}px ${backgroundHeight}px`
  }

  setTime(time: string) {
    this._textBlock.innerText = time;
  }

  showAtPercent(percent: number) {
    this.showAt(percent / 100 * this._engine.getDurationTime());
  }

  clear() {
    this._node.style.background = '';
  }

  destroy(): void {
    this._engine = null;
  }
}


